/* Вкладка чатов */

/* Сетка чатов */
.chats-grid {
  display: flex;
  flex-direction: column;
  gap: 20px;
  padding: 0;
  margin-top: var(--spacing-xxl);
  transition: all var(--transition-normal);
}

/* Плавные переходы для предотвращения ошибок ResizeObserver */
.chats-tab {
  transition: all var(--transition-normal);
  min-height: 200px;
}