/* Градиентный блок для плавного перехода */
.bottom-gradient-overlay {
  position: fixed;
  bottom: 0;
  left: 0;
  right: 0;
  height: var(--gradient-overlay-height);
  background: linear-gradient(180deg, rgba(18, 18, 18, 0) 30%, var(--color-dark) 100%);
  z-index: 999;
  pointer-events: none;
}

/* Нижняя навигация */
.bottom-nav {
  position: fixed;
  bottom: var(--spacing-nav-bottom);
  left: 50%;
  transform: translateX(-50%);
  width: calc(100% - 48px);
  max-width: calc(var(--app-max-width) - 48px);
  height: var(--nav-height);
  background: rgba(26, 26, 26, 0.7);
  backdrop-filter: blur(9.4px);
  border: 1px solid var(--color-overlay);
  border-radius: var(--border-radius-large);
  display: flex;
  justify-content: space-around;
  align-items: center;
  z-index: 1000;
  box-shadow: var(--shadow-xl);
}

.nav-item {
  background: none;
  border: none;
  color: var(--color-gray-medium);
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: var(--spacing-xs);
  cursor: pointer;
  transition: all var(--transition-normal);
  padding: var(--spacing-sm) var(--spacing-md);
  border-radius: var(--border-radius-large);
  min-width: var(--nav-item-min-width);
  position: relative;
}

.nav-item:hover {
  color: var(--color-gray-lighter);
  transform: translateY(-1px);
}

.nav-item.active {
  color: var(--color-white);
}

.nav-icon {
  width: var(--nav-icon-size);
  height: var(--nav-icon-size);
  opacity: 1;
  transition: all var(--transition-normal);
  filter: brightness(0) invert(0.6);
  pointer-events: none;
}

.nav-icon.active {
  opacity: 1;
  filter: brightness(0) invert(1);
}

.nav-item:hover .nav-icon {
  filter: brightness(0) invert(0.7);
}

.nav-label {
  font-family: var(--font-family);
  font-size: var(--nav-label-size);
  font-weight: var(--font-weight-medium);
  line-height: var(--line-height-normal);
  transition: all var(--transition-normal);
  pointer-events: none;
}

 