/* Загрузочный экран */
.loading-screen {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: var(--viewport-height, 100vh);
  background: var(--color-black);
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 1000;
}

.loading-screen::before {
  content: '';
  position: absolute;
  top: 0;
  left: 50%;
  transform: translateX(-50%);
  width: 100%;
  max-width: var(--app-max-width);
  height: 100%;
  background: var(--color-black);
}

.loading-video {
  position: absolute;
  top: 0;
  left: 50%;
  transform: translateX(-50%);
  width: 100%;
  max-width: var(--app-max-width);
  height: 100%;
  object-fit: cover;
  object-position: center;
  /* Оптимизация для видео */
  -webkit-backface-visibility: hidden;
  backface-visibility: hidden;
  transform: translateZ(0);
  -webkit-transform: translateZ(0);
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.loading-image {
  position: absolute;
  top: 0;
  left: 50%;
  transform: translateX(-50%);
  width: 100%;
  max-width: var(--app-max-width);
  height: 100%;
  object-fit: cover;
  object-position: center;
  /* Оптимизация для изображений */
  image-rendering: -webkit-optimize-contrast;
  image-rendering: crisp-edges;
  image-rendering: pixelated;
  -webkit-backface-visibility: hidden;
  backface-visibility: hidden;
  transform: translateZ(0);
  -webkit-transform: translateZ(0);
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  shape-rendering: geometricPrecision;
}

/* Оптимизация для высоких DPI дисплеев */
@media (-webkit-min-device-pixel-ratio: 2), (min-resolution: 192dpi) {
  .loading-image {
    image-rendering: -webkit-optimize-contrast;
    image-rendering: crisp-edges;
    -webkit-transform: translateZ(0) scale(1);
    transform: translateZ(0) scale(1);
  }
}

/* Оптимизация для очень высоких DPI дисплеев */
@media (-webkit-min-device-pixel-ratio: 3), (min-resolution: 288dpi) {
  .loading-image {
    image-rendering: -webkit-optimize-contrast;
    image-rendering: crisp-edges;
  }
}

.loading-content {
  position: relative;
  z-index: 1;
  height: 100vh;
  width: 100%;
  max-width: var(--app-max-width);
  margin: 0 auto;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  padding: var(--loading-content-padding);
  box-sizing: border-box;
}

.loading-text {
  margin-top: 10px;
  width: 100%;
}

.loading-title {
  color: var(--color-white);
  margin: 0 0 var(--spacing-xxl) 0;
  font-family: var(--font-family);
  font-size: 42px;
  font-weight: 600;
  line-height: 36px;
  display: flex;
  flex-direction: column;
  gap: var(--spacing-loading-title-gap);
}

.title-line {
  display: block;
}

.brand-line {
  display: flex;
  align-items: center;
  gap: var(--spacing-sm);
}

.inline-logo {
  width: 37px;
  height: 37px;
}

.loading-description {
  color: var(--color-white);
  font-size: var(--loading-desc-size);
  line-height: 1.15;
  margin: 0;
  max-width: 400px;
  font-family: var(--font-family);
  opacity: 0.8;
}

.loading-cta-btn {
  background: var(--color-white);
  color: var(--color-secondary);
  border: none;
  padding: 16px 0;
  border-radius: var(--border-radius-large);
  font-family: var(--font-family);
  font-size: 20px;
  font-weight: 600;
  line-height: 1.193;
  letter-spacing: -0.03em;
  cursor: pointer;
  transition: all var(--transition-normal);
  display: flex;
  align-items: center;
  justify-content: center;
  gap: var(--spacing-loading-btn-gap);
  width: 100%;
  max-width: var(--loading-btn-width);
  height: 55px;
  box-shadow: 0px 4px 40px 0px var(--color-overlay-white);
  margin-bottom: 20px;
}

.loading-cta-btn:hover {
  transform: translateY(-2px);
  box-shadow: var(--shadow-loading-btn-hover);
}

.btn-icon {
  width: 30px;
  height: 30px;
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 50%;
}

.btn-icon img {
  width: 100%;
  height: 100%;
  object-fit: contain;
}

 