/* FAQ компонент */
@import '../styles/components.css';

.faq-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 100%;
}

.faq-list {
  width: 100%;
  max-width: var(--app-max-width);
  display: flex;
  flex-direction: column;
  gap: var(--spacing-md);
}

/* Стили плашек (дублируются с AboutTab.css, можно вынести в общий файл в будущем) */
.faq-item {
  display: flex;
  flex-direction: column;
  background: var(--color-dark);
  border: 1px solid var(--color-overlay-light);
  border-radius: var(--border-radius-medium);
  cursor: pointer;
  transition: all var(--transition-normal);
  width: 100%;
  box-sizing: border-box;
  overflow: hidden;
}

.faq-item:hover {
  background: var(--color-gray-dark-light);
  border: 1px solid var(--color-overlay-light);
  transform: translateY(-1px);
}

.faq-question {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: var(--spacing-md) var(--spacing-lg);
  width: 100%;
  box-sizing: border-box;
}

.question-text {
  flex: 1;
  color: var(--color-white);
  font-family: var(--font-family);
  font-size: var(--font-size-xl);
  font-weight: var(--font-weight-medium);
  line-height: var(--line-height-normal);
  text-align: left;
}

.question-arrow {
  display: flex;
  align-items: center;
  justify-content: center;
  width: var(--spacing-xxl);
  height: var(--spacing-xxl);
  flex-shrink: 0;
  margin-left: var(--spacing-md);
  rotate: 180deg;
}

.question-arrow img {
  width: var(--nav-arrow-size);
  height: var(--nav-arrow-size);
  filter: brightness(0) invert(0.6);
  transition: all var(--transition-normal);
}

.faq-item:hover .question-arrow img {
  filter: brightness(0) invert(1);
}

/* Поворот стрелки при открытии */
.question-arrow img.rotated {
  transform: rotate(90deg);
}

/* Ответ */
.faq-answer {
  padding: 0 var(--spacing-lg) var(--spacing-lg) var(--spacing-lg);
  color: var(--color-text-muted);
  font-family: var(--font-family);
  font-size: var(--font-size-md);
  line-height: var(--line-height-loose);
  text-align: left;
  animation: slideDown 0.3s ease-out;
}

@keyframes slideDown {
  from {
    opacity: 0;
    transform: translateY(-10px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

/* Пустое состояние FAQ */
.faq-empty {
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 40px var(--spacing-xxl);
  text-align: center;
}

.faq-empty p {
  color: var(--color-gray-lightest);
  font-family: var(--font-family);
  font-size: var(--font-size-md);
  line-height: var(--line-height-loose);
  margin: 0;
}

/* Стили для маркированных списков в FAQ */
.faq-answer ul {
  margin: 0;
  padding-left: 20px;
  list-style-type: disc;
}

.faq-answer li {
  margin-bottom: 8px;
  color: var(--color-text-muted);
  font-family: var(--font-family);
  font-size: var(--font-size-md);
  line-height: var(--line-height-loose);
}

.faq-answer li:last-child {
  margin-bottom: 0;
}