/* Главная вкладка */
.home-tab {
  display: flex;
  flex-direction: column;
  gap: 0;
  align-items: center;
  transition: all var(--transition-normal);
  min-height: 200px;
}

/* Сетка разделов */
.sections-grid {
  display: flex;
  flex-direction: column;
  gap: 20px;
  padding: 0;
  width: 100%;
  max-width: var(--app-max-width);
}

.no-materials {
  text-align: center;
  color: var(--color-gray-lightest);
  font-style: italic;
  padding: 40px var(--spacing-xxl);
  font-family: var(--font-family);
} 