/* Поисковая строка */
.search-container {
  background: transparent;
  border-bottom: none;
  padding: var(--spacing-search-top) 0 0;
  margin-bottom: 30px;
  width: 100%;
  display: block;
  max-width: var(--app-max-width);
  margin-left: auto;
  margin-right: auto;
  box-sizing: border-box;
}

.search-wrapper {
  display: flex;
  align-items: center;
  gap: var(--spacing-sm);
  width: 100%;
  padding: 0 24px;
  box-sizing: border-box;
  min-width: 0; /* Важно для Firefox */
  flex-wrap: nowrap; /* Предотвращает перенос */
}

.search-bar {
  flex: 1;
  min-width: 0; /* Важно для Firefox */
  display: flex;
  align-items: center;
  background: var(--color-gray-dark);
  border-radius: var(--border-radius-large);
  padding: 0 18px;
  border: none;
  transition: all var(--transition-normal);
  height: 40px;
  overflow: hidden; /* Предотвращает выход за границы */
}

.search-bar:focus-within {
  border-color: transparent;
  box-shadow: var(--shadow-search-focus);
}

.search-icon {
  width: 24px;
  height: 24px;
  opacity: 0.6;
  margin-right: var(--spacing-sm);
  filter: brightness(0) invert(0.6);
}

.search-bar input {
  flex: 1;
  min-width: 0; /* Важно для Firefox */
  background: none;
  border: none;
  color: var(--color-gray-light);
  font-family: var(--font-family);
  font-size: 18px;
  font-weight: var(--font-weight-medium);
  line-height: var(--line-height-normal);
  outline: none;
  width: 100%; /* Гарантирует правильную ширину */
}

.search-bar input::placeholder {
  color: var(--color-gray-light);
  font-family: var(--font-family);
  font-size: 18px;
  font-weight: var(--font-weight-medium);
  line-height: var(--line-height-normal);
}

.clear-search-btn {
  background: none;
  border: none;
  color: var(--color-gray-lightest);
  cursor: pointer;
  padding: var(--spacing-xs);
  border-radius: 50%;
  transition: all var(--transition-fast);
  font-size: var(--font-size-md);
}

.clear-search-btn:hover {
  color: var(--color-white);
}

/* Кнопка "Назад" */
.back-search-btn {
  background: none;
  border: none;
  color: var(--color-white);
  padding: 6px var(--spacing-sm);
  border-radius: var(--border-radius-medium);
  font-size: 18px;
  font-weight: var(--font-weight-medium);
  cursor: pointer;
  transition: all var(--transition-fast);
  display: flex;
  align-items: center;
  gap: var(--spacing-xs);
  flex-shrink: 0;
  font-family: var(--font-family);
  white-space: nowrap; /* Предотвращает перенос текста */
  max-width: 120px; /* Ограничивает максимальную ширину */
}

.back-search-btn:hover {
  background: rgba(255, 255, 255, 0.1);
}

.back-icon {
  width: 15px;
  height: 15px;
  opacity: 0.8;
  transform: rotate(0deg);
}

.back-search-btn:hover .back-icon {
  opacity: 1;
}