/* Вкладка "О нас" */
@import '../../styles/components.css';

.about-tab {
  display: flex;
  flex-direction: column;
  align-items: center;
}

/* Ссылки */
.about-links {
  width: 100%;
  max-width: var(--app-max-width);
  display: flex;
  flex-direction: column;
  gap: var(--spacing-md);
}

/* Стили плашек (дублируются с FAQ.css, можно вынести в общий файл в будущем) */
.about-link {
  display: flex;
  align-items: center;
  background: var(--color-dark);
  border: 1px solid var(--color-overlay-light);
  border-radius: var(--border-radius-medium);
  padding: var(--spacing-md) var(--spacing-lg);
  cursor: pointer;
  transition: all var(--transition-normal);
  width: 100%;
  box-sizing: border-box;
}

.about-link:hover {
  background: var(--color-gray-dark-light);
  border: 1px solid var(--color-overlay-light);
  transform: translateY(-1px);
}

.link-icon {
  display: flex;
  align-items: center;
  justify-content: center;
  width: var(--icon-size-xl);
  height: var(--icon-size-xl);
  margin-right: var(--spacing-md);
  flex-shrink: 0;
}

.link-icon img {
  width: var(--icon-size-xxl);
  height: var(--icon-size-xxl);
}

.link-text {
  flex: 1;
  color: var(--color-white);
  font-family: var(--font-family);
  font-size: var(--font-size-xl);
  font-weight: var(--font-weight-medium);
  line-height: var(--line-height-normal);
}

.link-arrow {
  display: flex;
  align-items: center;
  justify-content: center;
  width: var(--spacing-xxl);
  height: var(--spacing-xxl);
  flex-shrink: 0;
  transform: rotate(180deg);
}

.link-arrow img {
  width: var(--nav-arrow-size);
  height: var(--nav-arrow-size);
  filter: brightness(0) invert(0.6);
  transition: all var(--transition-normal);
}

.about-link:hover .link-arrow img {
  filter: brightness(0) invert(1);
}

/* Контент экрана */
.screen-content {
  width: 100%;
  max-width: var(--app-max-width);
  border-radius: var(--border-radius-medium);
  color: var(--color-text-muted);
  font-family: var(--font-family);
}

.screen-content h3 {
  color: var(--color-accent-blue);
  margin-bottom: var(--spacing-lg);
  font-family: var(--font-family);
  font-size: var(--font-size-xxl);
  font-weight: var(--font-weight-semibold);
}

.screen-content p {
  color: var(--color-text-muted);
  line-height: var(--line-height-loose);
  font-family: var(--font-family);
  margin-bottom: var(--spacing-md);
}

.screen-content ul {
  margin: var(--spacing-lg) 0;
  padding-left: var(--spacing-lg);
}

.screen-content li {
  color: var(--color-text-muted);
  line-height: var(--line-height-loose);
  font-family: var(--font-family);
  margin-bottom: var(--spacing-sm);
}



