/* Глобальные оптимизации для изображений */
/* Базовые оптимизации для всех изображений */
img {
  image-rendering: var(--image-rendering);
  image-rendering: var(--image-smoothing);
  image-rendering: pixelated;
  -webkit-backface-visibility: var(--backface-visibility);
  backface-visibility: var(--backface-visibility);
  transform: var(--transform-3d);
  -webkit-transform: var(--webkit-transform-3d);
  -webkit-font-smoothing: var(--font-smoothing);
  -moz-osx-font-smoothing: var(--moz-font-smoothing);
  shape-rendering: var(--shape-rendering);
  max-width: 100%;
  height: auto;
}

/* Оптимизации для изображений высокого качества */
.high-quality-image {
  image-rendering: var(--image-rendering);
  image-rendering: var(--image-smoothing);
  image-rendering: pixelated;
  -webkit-backface-visibility: var(--backface-visibility);
  backface-visibility: var(--backface-visibility);
  transform: var(--transform-3d);
  -webkit-transform: var(--webkit-transform-3d);
  -webkit-font-smoothing: var(--font-smoothing);
  -moz-osx-font-smoothing: var(--moz-font-smoothing);
  shape-rendering: var(--shape-rendering);
  will-change: var(--will-change-transform);
}

/* Оптимизации для placeholder изображений */
.placeholder-image {
  image-rendering: var(--image-rendering);
  image-rendering: var(--image-smoothing);
  -webkit-backface-visibility: var(--backface-visibility);
  backface-visibility: var(--backface-visibility);
  transform: var(--transform-3d);
  -webkit-transform: var(--webkit-transform-3d);
}

/* Оптимизации для Retina дисплеев */
@media (-webkit-min-device-pixel-ratio: 2), (min-resolution: 192dpi) {
  img {
    image-rendering: -webkit-optimize-contrast;
    image-rendering: crisp-edges;
  }
  
  .high-quality-image {
    transform: translateZ(0) scale(1);
  }
}

/* Оптимизации для Ultra Retina дисплеев */
@media (-webkit-min-device-pixel-ratio: 3), (min-resolution: 288dpi) {
  img {
    image-rendering: -webkit-optimize-contrast;
    image-rendering: crisp-edges;
  }
  
  .high-quality-image {
    transform: translateZ(0) scale(1);
  }
}

/* Глобальные стили для устранения синей рамки при touch-событиях */
button, 
.nav-item, 
.clickable, 
[role="button"], 
[tabindex] {
  /* Убираем синюю рамку при touch-событиях */
  outline: none;
  -webkit-tap-highlight-color: transparent;
  -webkit-touch-callout: none;
  -webkit-user-select: none;
  -khtml-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
  
  /* Улучшаем touch-поведение */
  touch-action: manipulation;
}

/* Убираем синюю рамку при фокусе */
button:focus, 
.nav-item:focus, 
.clickable:focus, 
[role="button"]:focus, 
[tabindex]:focus {
  outline: none;
  -webkit-tap-highlight-color: transparent;
}

/* Убираем синюю рамку при активном состоянии */
button:active, 
.nav-item:active, 
.clickable:active, 
[role="button"]:active, 
[tabindex]:active {
  outline: none;
  -webkit-tap-highlight-color: transparent;
}

/* Убираем синюю рамку при hover */
button:hover, 
.nav-item:hover, 
.clickable:hover, 
[role="button"]:hover, 
[tabindex]:hover {
  outline: none;
  -webkit-tap-highlight-color: transparent;
}

/* Специальные стили для ссылок */
a {
  -webkit-tap-highlight-color: transparent;
  -webkit-touch-callout: none;
  -webkit-user-select: none;
  -khtml-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
}

a:focus,
a:active,
a:hover {
  outline: none;
  -webkit-tap-highlight-color: transparent;
}

/* Стили для карточек и других интерактивных элементов */
.card,
.material-card,
.section-card,
.clickable-element {
  -webkit-tap-highlight-color: transparent;
  -webkit-touch-callout: none;
  -webkit-user-select: none;
  -khtml-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
  touch-action: manipulation;
}

.card:focus,
.material-card:focus,
.section-card:focus,
.clickable-element:focus {
  outline: none;
  -webkit-tap-highlight-color: transparent;
}

.card:active,
.material-card:active,
.section-card:active,
.clickable-element:active {
  outline: none;
  -webkit-tap-highlight-color: transparent;
} 