/* Стили для управления viewport'ом и высотой экрана */

/* CSS переменные для viewport'а */
:root {
  --viewport-height: var(--tg-viewport-height, 100vh);
  --viewport-stable-height: var(--tg-viewport-stable-height, 100vh);
}

/* Основные контейнеры с правильной высотой */
.App {
  min-height: var(--viewport-height, 100vh);
  height: var(--viewport-height, 100vh);
  overflow: hidden;
}

.app-container {
  height: var(--viewport-height, 100vh);
  min-height: var(--viewport-height, 100vh);
  overflow: hidden;
}

.loading-screen {
  height: var(--viewport-height, 100vh);
  min-height: var(--viewport-height, 100vh);
}

/* Fallback для Telegram WebApp без CSS переменных */
@media (max-height: 100vh) {
  .App {
    height: calc(100vh - 20px);
    min-height: calc(100vh - 20px);
  }
  
  .app-container {
    height: calc(100vh - 20px);
    min-height: calc(100vh - 20px);
  }
  
  .loading-screen {
    height: calc(100vh - 20px);
    min-height: calc(100vh - 20px);
  }
}

/* Поддержка для устройств с вырезами (notch) */
@supports (padding-top: env(safe-area-inset-top)) {
  .App {
    padding-top: env(safe-area-inset-top);
    padding-bottom: env(safe-area-inset-bottom);
    padding-left: env(safe-area-inset-left);
    padding-right: env(safe-area-inset-right);
  }
}
