/* Карточка материала */
.material-card {
  border-radius: 20px;
  overflow: hidden;
  cursor: pointer;
  transition: all var(--transition-normal);
  display: flex;
  flex-direction: column;
  width: 100%;
  max-width: var(--app-max-width);
  position: relative;
}

.material-card:hover {
  transform: translateY(-2px);
  box-shadow: var(--shadow-lg);
}

/* Изображение материала */
.material-image {
  position: relative;
  width: 100%;
  height: 155px;
  overflow: hidden;
  border-radius: 20px;
  /* Оптимизация для высоких DPI дисплеев */
  -webkit-backface-visibility: hidden;
  backface-visibility: hidden;
  transform: translateZ(0);
  -webkit-transform: translateZ(0);
}

.material-image img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  object-position: center;
  transition: transform var(--transition-normal), opacity var(--transition-normal);
  /* Улучшение качества рендеринга изображений */
  image-rendering: -webkit-optimize-contrast;
  image-rendering: crisp-edges;
  image-rendering: pixelated;
  -webkit-backface-visibility: hidden;
  backface-visibility: hidden;
  transform: translateZ(0);
  -webkit-transform: translateZ(0);
  /* Предотвращение размытия при масштабировании */
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  /* Оптимизация производительности */
  will-change: transform, opacity;
  /* Улучшение качества для векторных изображений */
  shape-rendering: geometricPrecision;
}

/* Оптимизация для высоких DPI дисплеев */
@media (-webkit-min-device-pixel-ratio: 2), (min-resolution: 192dpi) {
  .material-image img {
    image-rendering: -webkit-optimize-contrast;
    image-rendering: crisp-edges;
    /* Дополнительная оптимизация для Retina дисплеев */
    -webkit-transform: translateZ(0) scale(1);
    transform: translateZ(0) scale(1);
  }
}

/* Оптимизация для очень высоких DPI дисплеев */
@media (-webkit-min-device-pixel-ratio: 3), (min-resolution: 288dpi) {
  .material-image img {
    image-rendering: -webkit-optimize-contrast;
    image-rendering: crisp-edges;
  }
}

.material-image-loaded {
  opacity: 0;
  transition: opacity var(--transition-normal);
  will-change: opacity;
}

.material-card:hover .material-image img {
  transform: scale(1.05) translateZ(0);
  -webkit-transform: scale(1.05) translateZ(0);
}

.material-image-placeholder {
  border-radius: 20px;
  width: 100%;
  height: 100%;
  background: var(--color-secondary);
  /* Оптимизация для placeholder изображений */
  -webkit-backface-visibility: hidden;
  backface-visibility: hidden;
  transform: translateZ(0);
  -webkit-transform: translateZ(0);
}

.placeholder-material {
  width: 100%;
  height: 100%;
  object-fit: cover;
  object-position: center;
  border-radius: 20px;
  /* Улучшение качества placeholder изображений */
  image-rendering: -webkit-optimize-contrast;
  image-rendering: crisp-edges;
  -webkit-backface-visibility: hidden;
  backface-visibility: hidden;
  transform: translateZ(0);
  -webkit-transform: translateZ(0);
}

/* Кнопка избранного */
.favorite-btn-container {
  position: absolute;
  top: 13px;
  right: 13px;
  z-index: 10;
}

.favorite-btn {
  background: var(--color-dark);
  border: none;
  cursor: pointer;
  padding: 0;
  border-radius: 50%;
  transition: all var(--transition-normal);
  display: flex;
  align-items: center;
  justify-content: center;
  width: 30px;
  height: 30px;
  /* Оптимизация для кнопок */
  -webkit-backface-visibility: hidden;
  backface-visibility: hidden;
  transform: translateZ(0);
  -webkit-transform: translateZ(0);
}

.favorite-btn:hover {
  transform: scale(1.1) translateZ(0);
  -webkit-transform: scale(1.1) translateZ(0);
  background: var(--color-dark-light);
}

.favorite-btn.active {
  animation: favoritePulse var(--transition-fast) ease;
}

.favorite-btn.active:hover {
  transform: scale(1.1) translateZ(0);
  -webkit-transform: scale(1.1) translateZ(0);
}

.favorite-btn .favorite-icon {
  width: 19px;
  height: 19px;
  min-width: 19px;
  min-height: 19px;
  max-width: 19px;
  max-height: 19px;
  box-sizing: border-box;
  display: block;
  flex-shrink: 0;
  transition: all var(--transition-normal);
  filter: brightness(0) invert(1);
  /* Оптимизация для иконок */
  -webkit-backface-visibility: hidden;
  backface-visibility: hidden;
  transform: translateZ(0);
  -webkit-transform: translateZ(0);
}

.favorite-btn.active .favorite-icon {
  filter: none;
  color: var(--color-favorite);
}

@keyframes favoritePulse {
  0% { transform: scale(1) translateZ(0); }
  50% { transform: scale(1.2) translateZ(0); }
  100% { transform: scale(1) translateZ(0); }
}

/* Контент карточки */
.material-content {
  display: flex;
  flex-direction: column;
  padding-top: 24px;
  flex: 1;
}

/* Заголовок */
.material-title {
  margin: 0 0 24px 0;
  font-family: var(--font-family);
  font-size: 22px;
  font-weight: var(--font-weight-medium);
  line-height: 1.2;
  letter-spacing: -0.02em;
  white-space: pre-line;
}

/* Кнопка открытия */
.open-btn {
  color: var(--color-white);
  border: none;
  padding: 10px 0;
  border-radius: 90px;
  font-family: var(--font-family);
  font-size: 20px;
  font-weight: var(--font-weight-medium);
  line-height: var(--line-height-normal);
  cursor: pointer;
  transition: all var(--transition-fast);
  width: 100%;
  height: 40px;
  text-align: center;
  display: flex;
  align-items: center;
  justify-content: center;
}

.open-btn:hover {
  transform: translateY(-1px) translateZ(0);
  -webkit-transform: translateY(-1px) translateZ(0);
  box-shadow: var(--shadow-primary-btn);
}

/* Состояния для карточек без внешних ссылок */
.material-card:not([onclick]) {
  cursor: default;
}

.material-card:not([onclick]):hover {
  transform: none;
  box-shadow: var(--shadow-sm);
}

/* Мобильные стили для карточек материалов */
@media (max-width: 576px) {
  /* Мобильные стили уже включены в основные стили */
}

/* Анимация загрузки */
.material-card.loading {
  animation: shimmer 1.5s infinite;
}

/* Черный слой для заблокированных материалов */
.material-overlay {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: rgba(0, 0, 0, 0.5);
  border-radius: var(--border-radius-medium);
  z-index: 2;
}

/* Контейнер для иконки замка */
.lock-icon-container {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  z-index: 3;
  display: flex;
  align-items: center;
  justify-content: center;
  width: 100px;
  height: 100px;
}

/* Иконка замка */
.lock-icon {
  width: 50px;
  height: 50px;
  /* Оптимизация для иконок */
  -webkit-backface-visibility: hidden;
  backface-visibility: hidden;
  transform: translateZ(0);
  -webkit-transform: translateZ(0);
} 