/* Баннер раздела */
.section-banner {
  background: transparent;
  border-radius: var(--border-radius-medium);
  overflow: hidden;
  transition: all var(--transition-normal);
  height: calc(var(--section-card-height) * 1.75);
  position: relative;
  width: 100%;
  max-width: var(--app-max-width);
  box-sizing: border-box;
  margin-bottom: 20px;
  cursor: pointer;
  /* Оптимизация для высоких DPI дисплеев */
  -webkit-backface-visibility: hidden;
  backface-visibility: hidden;
  transform: translateZ(0);
  -webkit-transform: translateZ(0);
}

.banner-background {
  width: 100%;
  height: 100%;
  position: relative;
  display: flex;
  align-items: center;
  border-radius: var(--border-radius-medium);
  background-color: var(--color-dark);
  /* Оптимизация для фона */
  -webkit-backface-visibility: hidden;
  backface-visibility: hidden;
  transform: translateZ(0);
  -webkit-transform: translateZ(0);
}

.banner-image {
  width: 100%;
  height: 100%;
  object-fit: cover;
  object-position: center;
  border-radius: var(--border-radius-medium);
  position: absolute;
  top: 0;
  left: 0;
  z-index: 1;
  transition: opacity var(--transition-normal);
  will-change: opacity;
  /* Улучшение качества рендеринга изображений */
  image-rendering: -webkit-optimize-contrast;
  image-rendering: crisp-edges;
  image-rendering: pixelated;
  -webkit-backface-visibility: hidden;
  backface-visibility: hidden;
  transform: translateZ(0);
  -webkit-transform: translateZ(0);
  /* Предотвращение размытия при масштабировании */
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  /* Улучшение качества для векторных изображений */
  shape-rendering: geometricPrecision;
}

/* Контент баннера */
.banner-content {
  position: relative;
  z-index: 2;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  width: 70%;
  height: 100%;
  padding: var(--spacing-lg);
  text-align: center;
}

/* Заголовок баннера */
.banner-title {
  position: absolute;
  top: var(--spacing-lg);
  left: var(--spacing-lg);
  font-family: var(--font-family);
  font-size: var(--font-size-display-lg);
  font-weight: var(--font-weight-bold);
  color: var(--color-white);
  margin: 0;
  padding: 0;
  line-height: var(--line-height-tight);
  text-shadow: var(--shadow-text);
  /* Оптимизация для текста */
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  /* Предотвращение выделения текста */
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
  /* Улучшение читаемости */
  letter-spacing: -0.02em;
  /* Ограничение ширины */
  max-width: 70%;
  text-align: left;
}

/* Оптимизация для высоких DPI дисплеев */
@media (-webkit-min-device-pixel-ratio: 2), (min-resolution: 192dpi) {
  .banner-image {
    image-rendering: -webkit-optimize-contrast;
    image-rendering: crisp-edges;
    /* Дополнительная оптимизация для Retina дисплеев */
    -webkit-transform: translateZ(0) scale(1);
    transform: translateZ(0) scale(1);
  }
  
  .banner-title {
    /* Дополнительная оптимизация для Retina дисплеев */
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
  }
}

/* Оптимизация для очень высоких DPI дисплеев */
@media (-webkit-min-device-pixel-ratio: 3), (min-resolution: 288dpi) {
  .banner-image {
    image-rendering: -webkit-optimize-contrast;
    image-rendering: crisp-edges;
  }
}

.banner-image-loaded {
  opacity: 0;
  transition: opacity var(--transition-normal);
}