/* Общие стили компонентов */

/* Базовые стили для карточек */
.card {
  background: rgba(43, 43, 43, 0.5);
  border-radius: var(--border-radius-medium);
  overflow: hidden;
  cursor: pointer;
  transition: all var(--transition-normal);
  position: relative;
}

.card:hover {
  transform: translateY(-2px);
  box-shadow: var(--shadow-lg);
}

.card:not([onclick]) {
  cursor: default;
}

.card:not([onclick]):hover {
  transform: none;
  box-shadow: var(--shadow-sm);
}

/* Базовые стили для кнопок */
.btn {
  background: none;
  border: none;
  cursor: pointer;
  padding: 0;
  border-radius: 50%;
  transition: all var(--transition-normal);
  display: flex;
  align-items: center;
  justify-content: center;
}

.btn:hover {
  transform: scale(1.1);
}

/* Базовые стили для заголовков */
.heading {
  margin: 0;
  font-family: 'SF Pro Display', -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, sans-serif;
  font-weight: var(--font-weight-semibold);
  line-height: var(--line-height-relaxed);
  letter-spacing: -0.03em;
}

.heading-xl {
  font-size: var(--font-size-display-lg);
  font-weight: var(--font-weight-bold);
  line-height: var(--line-height-tight);
}

/* Базовые стили для сеток */
.grid {
  display: grid;
  gap: var(--spacing-xxl);
  padding: var(--spacing-xxl) 0;
  justify-items: center;
}

.grid-materials {
  grid-template-columns: repeat(auto-fill, minmax(var(--card-width), 1fr));
}

.materials-grid {
  display: grid;
  grid-template-columns: 1fr;
  gap: 50px;
  justify-items: center;
  width: 100%;
  max-width: var(--app-max-width);
}

/* Базовые стили для контейнеров */
.container {
  display: flex;
  flex-direction: column;
  gap: 0;
  padding: 0 var(--spacing-xxxl);
}

/* Базовые стили для табов */
.tab-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 100%;
}

.tab-list {
  width: 100%;
  max-width: var(--app-max-width);
  display: flex;
  flex-direction: column;
  gap: var(--spacing-md);
}

/* Базовые стили для плашек */
.plate-item {
  display: flex;
  align-items: center;
  background: var(--color-dark);
  border: 1px solid var(--color-overlay-light);
  border-radius: var(--border-radius-medium);
  padding: var(--spacing-md) var(--spacing-lg);
  cursor: pointer;
  transition: all var(--transition-normal);
  width: 100%;
  box-sizing: border-box;
}

.plate-item:hover {
  background: var(--color-gray-dark-light);
  border: 1px solid var(--color-overlay-light);
  transform: translateY(-1px);
}

/* Базовые стили для FAQ элементов */
.faq-item-base {
  display: flex;
  flex-direction: column;
  background: var(--color-dark);
  border: 1px solid var(--color-overlay-light);
  border-radius: var(--border-radius-medium);
  cursor: pointer;
  transition: all var(--transition-normal);
  width: 100%;
  box-sizing: border-box;
  overflow: hidden;
}

.faq-item-base:hover {
  background: var(--color-gray-dark-light);
  border: 1px solid var(--color-overlay-light);
  transform: translateY(-1px);
}

/* Базовые стили для заголовков разделов */
.section-header {
  background: var(--color-gray-dark);
  color: var(--color-white);
  padding: var(--spacing-sm) var(--spacing-md);
  border-radius: var(--border-radius-xl);
  margin-bottom: 30px;
  position: relative;
  text-align: center;
  display: flex;
  align-items: center;
  justify-content: center;
  width: 100%;
  max-width: var(--app-max-width);
  height: 40px;
  box-sizing: border-box;
}

.section-header h2 {
  margin: 0;
  font-family: var(--font-family);
  font-size: var(--font-size-display);
  font-weight: var(--font-weight-semibold);
  line-height: var(--line-height-relaxed);
  letter-spacing: -0.03em;
  color: var(--color-white);
}

/* Базовые стили для состояний */
.empty-state {
  text-align: center;
  color: var(--color-gray-lightest);
  font-style: italic;
  padding: 40px var(--spacing-xxl);
  font-family: 'SF Pro Display', -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, sans-serif;
}

.loading {
  animation: shimmer 1.5s infinite;
}

@keyframes shimmer {
  0% { opacity: 0.7; }
  50% { opacity: 1; }
  100% { opacity: 0.7; }
}

.disabled {
  opacity: 0.6;
  cursor: not-allowed;
  pointer-events: none;
}

.disabled:hover {
  transform: none;
  border-color: transparent;
  box-shadow: none;
}

/* Мобильные стили по умолчанию */
@media (max-width: 576px) {

  .grid-sections {
    gap: var(--spacing-md);
    margin-top: 180px;
  }

  .grid-materials {
    grid-template-columns: 1fr;
    gap: var(--spacing-lg);
  }

  .heading-lg {
    font-size: var(--font-size-xxxl);
  }

  .btn-primary {
    font-size: var(--font-size-lg);
    padding: var(--spacing-md) 0;
  }
} 