/* Карточка раздела */
.section-card {
  background: transparent;
  border-radius: var(--border-radius-medium);
  overflow: hidden;
  cursor: pointer;
  transition: all var(--transition-normal);
  height: var(--section-card-height);
  position: relative;
  width: 100%;
  max-width: var(--app-max-width);
  box-sizing: border-box;
  /* Оптимизация для высоких DPI дисплеев */
  -webkit-backface-visibility: hidden;
  backface-visibility: hidden;
  transform: translateZ(0);
  -webkit-transform: translateZ(0);
}

.section-card:hover {
  transform: translateY(-2px) translateZ(0);
  -webkit-transform: translateY(-2px) translateZ(0);
  box-shadow: var(--shadow-lg);
}

.section-background {
  width: 100%;
  height: 100%;
  position: relative;
  display: flex;
  align-items: center;
  justify-content: flex-start;
  border-radius: var(--border-radius-medium);
  background-color: var(--color-dark);
  /* Оптимизация для фона */
  -webkit-backface-visibility: hidden;
  backface-visibility: hidden;
  transform: translateZ(0);
  -webkit-transform: translateZ(0);
}

.section-image {
  width: 100%;
  height: 100%;
  object-fit: cover;
  object-position: center;
  border-radius: var(--border-radius-medium);
  position: absolute;
  top: 0;
  left: 0;
  z-index: 1;
  transition: opacity var(--transition-normal);
  will-change: opacity;
  /* Улучшение качества рендеринга изображений */
  image-rendering: -webkit-optimize-contrast;
  image-rendering: crisp-edges;
  image-rendering: pixelated;
  -webkit-backface-visibility: hidden;
  backface-visibility: hidden;
  transform: translateZ(0);
  -webkit-transform: translateZ(0);
  /* Предотвращение размытия при масштабировании */
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  /* Улучшение качества для векторных изображений */
  shape-rendering: geometricPrecision;
}

/* Оптимизация для высоких DPI дисплеев */
@media (-webkit-min-device-pixel-ratio: 2), (min-resolution: 192dpi) {
  .section-image {
    image-rendering: -webkit-optimize-contrast;
    image-rendering: crisp-edges;
    /* Дополнительная оптимизация для Retina дисплеев */
    -webkit-transform: translateZ(0) scale(1);
    transform: translateZ(0) scale(1);
  }
}

/* Оптимизация для очень высоких DPI дисплеев */
@media (-webkit-min-device-pixel-ratio: 3), (min-resolution: 288dpi) {
  .section-image {
    image-rendering: -webkit-optimize-contrast;
    image-rendering: crisp-edges;
  }
}

.section-image-loaded {
  opacity: 0;
  transition: opacity var(--transition-normal);
}

.section-overlay {
  background: transparent;
  padding: 0 var(--spacing-lg);
  text-align: left;
  width: 100%;
  height: 100%;
  display: flex;
  align-items: center;
  position: relative;
  z-index: 1;
}

.section-content {
  display: flex;
  flex-direction: column;
  justify-content: center;
  width: 70%;
  max-width: 70%;
}

.section-title {
  margin: 0;
  font-family: var(--font-family);
  font-size: var(--font-size-display-lg);
  font-weight: var(--font-weight-bold);
  line-height: var(--line-height-tight);
  letter-spacing: -0.03em;
  text-shadow: var(--shadow-text);
  white-space: normal;
  word-wrap: break-word;
  color: var(--custom-text-color, inherit) !important;
}

.section-description {
  margin: var(--spacing-sm) 0 0 0;
  font-family: var(--font-family);
  font-size: var(--font-size-base);
  font-weight: var(--font-weight-normal);
  line-height: var(--line-height-normal);
  text-shadow: var(--shadow-text);
  opacity: 0.8;
  white-space: normal;
  word-wrap: break-word;
  color: var(--custom-text-color, inherit) !important;
}

/* Мобильные стили для карточек разделов */
@media (max-width: 576px) {
  /* Мобильные стили уже включены в основные стили */
}

/* Черный слой для заблокированных чатов */
.section-overlay-dark {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: rgba(0, 0, 0, 0.7);
  border-radius: var(--border-radius-medium);
  z-index: 2;
}

/* Контейнер для иконки замка в чатах */
.section-lock-icon-container {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  z-index: 4;
  display: flex;
  align-items: center;
  justify-content: center;
  width: 60px;
  height: 60px;
  /* Оптимизация для контейнера иконки */
  -webkit-backface-visibility: hidden;
  backface-visibility: hidden;
  transform: translate(-50%, -50%) translateZ(0);
  -webkit-transform: translate(-50%, -50%) translateZ(0);
}

/* Иконка замка в чатах */
.section-lock-icon {
  width: 50px;
  height: 50px;
  /* Оптимизация для иконок */
  -webkit-backface-visibility: hidden;
  backface-visibility: hidden;
  transform: translateZ(0);
  -webkit-transform: translateZ(0);
} 